<script>
import { Dialog } from 'vant';
export default {
  name: "HelloWorld",
  data() {
    return {
      info:{
      name: "",
      grade: "",
      phone: ""
      },
      status:false,
      isSubmit:false,
      get:false,
      share:false,
      need:false,
      isC:false,
      can:true,
      selW:'请点击选择 ▼',
      ok:false,
      count:101,
      columns: ['一年级', '二年级', '三年级', '四年级', '五年级','六年级','七年级','八年级','九年级'],
      day:7,
      hour:22,
      min:42,
      award:'',
      aw:'VIP课程2h',
      already:false,
      index:9
    };
  },
  watch: {
    
  },
   methods:{
            go() {
              if(this.can){
                  this.can=false;
                  var that=this;
                   this.$fetch('http://choujiang.shidaitech.net/api/getAward')
                .then((res) => {
                     if(res.data.award_id==1){
                       var p=4;
                       that.aw='神秘公仔一个';
                     }else if(res.data.award_id==2){
                        var p=8;
                        that.aw='500元抵用券';
                     }else if(res.data.award_id==3){
                       var p=6;
                       that.aw='300元抵用券';
                     }else if(res.data.award_id==4){
                       var p=1;
                       that.aw='VIP课程2h';
                     }else {
                       var p=7;
                       that.aw='线上电影票';
                     }

                      var i=1,j=0;
                              var inte=setInterval(()=>{
                                if(j==48){
                                  i=1;
                                  that.index=i;
                                  clearInterval(inte);
                                  var inte1=setInterval(()=>{
                                      if(i==7){
                                      clearInterval(inte1);
                                      setTimeout(()=>{
                                        that.index=0;
                                      },100)
                                      var inte2=setInterval(()=>{
                                            if(i==7+p){
                                            clearInterval(inte2);
                                            that.index=i-8;
                                            setTimeout(()=>{
                                              that.index=9;
                                                setTimeout(()=>{
                                                  that.index=i-8;
                                                    setTimeout(()=>{
                                                      that.index=9;
                                                        setTimeout(()=>{
                                                        that.index=i-8;
                                                        that.need=true;
                                                        that.get=true;
                                                        that.isC=true;
                                                      },200) 
                                                    },200) 
                                                },200) 
                                            },200)
                                          
                                            }else{
                                            i++;
                                            that.index=i-8;
                                            }

                                            
                                        },300)
                                      }else{
                                      i++;
                                      that.index=i;
                                      }
                                  },150)
                                }else{
                                  j++;
                                  if(i==7){
                                  i=0
                                  }else{
                                  i++
                                  }
                                  that.index=i;
                                }
                              },50)
               })
                            
              }
              
            },
            submit(){
              var reg=/^\d{11}$/;
              var ok=reg.test(this.info.phone);
              var that=this;
              if(this.info.name==''){
                  Dialog.alert({
                    message: '请填写姓名'
                  }).then(() => {
                    // on close
                  });
              }else  if(this.info.grade==''){
                   Dialog.alert({
                    message: '请填写年级'
                  }).then(() => {
                    // on close
                  });
              }else  if(this.info.phone==''||!ok){
                   Dialog.alert({
                    message: '请填写11位手机号'
                  }).then(() => {
                    // on close
                  });
              }else{
                 this.$fetch('http://choujiang.shidaitech.net/api/getUserInfo',{name:this.info.name,grade:this.info.grade.slice(0,3),phone:this.info.phone})
                 .then((response) => {
                    that.isSubmit=true;
                    that.already=true;
                    that.info.grade=that.info.grade.slice(0,3);
                  Dialog.alert({
                    message: '报名成功'
                  }).then(() => {
                    // on close
                  });
                  })
                
              }
            },
            select(){
              if(!this.already){
             this.status=true;
             this.need=true;
             this.selW='请点击选择▲';
              }

            },
            open(){
              this.need=true;
              this.get=true;
            },
            onConfirm(value, index) {
               this.status=false;
               this.need=false;
               this.info.grade=value+" ▼";
            },
            onCancel() {
             this.status=false;
             this.need=false;
            },
            formate(){
              var timestamp = Date.parse(new Date());
              var t=1539359999000-timestamp;
               var day = parseInt(t / (1000 * 60 * 60 * 24));
              var hours = parseInt((t % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
              var minutes= parseInt((t % (1000 * 60 * 60)) / (1000 * 60));
              return day+','+hours+','+minutes
            },
            daoji(){
              var a=this.formate().split(',');
              this.day=a[0];
              this.hour=a[1];
              this.min=a[2];
            },
            close(){
              this.need=false;
              this.status=false;
              this.get=false;
            },
            close1(){
              this.share=false;
            },
            close2(){
              this.need=false;
              this.status=false;
              this.get=false;
            },
            shares(){
              this.share=true;
            }
        },
  mounted() {
            var that=this;
            this.$fetch('http://choujiang.shidaitech.net/api/checkUser')
                .then((res) => {
                    console.log(res)
                    that.count=res.data.num+101;
                    if(res.data.user==null){
                      that.isSubmit=false;
                      that.already=false;
                      that.can=true;
                    }else if(res.data.user.name){
                             that.isSubmit=true;
                             that.already=true;
                             that.info.name=res.data.user.name;
                             that.info.grade=res.data.user.grade;
                             that.info.phone=res.data.user.phone;
                             if(res.data.user.award_id){
                                  that.can=false;
                                    that.isC=true;
                                   if(res.data.user.award_id==1){
                                      that.aw='神秘公仔一个';
                                      that.index=3;
                                    }else if(res.data.user.award_id==2){
                                      that.aw='500元抵用券';
                                      that.index=7;
                                    }else if(res.data.user.award_id==3){
                                      that.aw='300元抵用券';
                                      that.index=5;
                                    }else if(res.data.user.award_id==4){
                                      that.aw='VIP课程2h';
                                      that.index=0;
                                    }else{
                                      that.aw='线上电影票';
                                      that.index=6;
                                    }
                             }else{
                                  that.can=true;
                                  that.isC=false;
                             }
                    }else{
                             that.isSubmit=false;
                             that.already=false;
                             if(res.data.user.award_id){
                                  that.can=false;
                                  that.isC=true;
                                  if(res.data.user.award_id==1){
                                      that.aw='神秘公仔一个';
                                      that.index=3;
                                    }else if(res.data.user.award_id==2){
                                      that.aw='500元抵用券';
                                      that.index=7;
                                    }else if(res.data.user.award_id==3){
                                      that.aw='300元抵用券';
                                      that.index=5;
                                    }else if(res.data.user.award_id==4){
                                      that.aw='VIP课程2h';
                                      that.index=0;
                                    }else{
                                      that.aw='线上电影票';
                                      that.index=6;
                                    }
                             }else{
                                  that.can=true;
                                  that.isC=false;
                             }
                    }
            })
            that.daoji()
            setInterval(()=>{
              that.daoji()
            },3000)
        },
};
</script>

