    bottom:-300px;
    width:100%;
    transition:all 0.35s linear;
  }
  .show{
    bottom:0px;
  }
  .commom{
    color:#3377FF;
    font-size:11px;
  }
  .hh div{
    font-size:16px;
    color:#80A7FF;
    margin-top:5px;
    font-weight:500;
  }
  .hh div.redW{
    font-size:12px;
    color:#FF8533;
      margin-top:2px;
  }
  .van-picker{
    z-index: 2;
  }
  .no{
    height:100vh;
    overflow: hidden;
  }
  .van-field__control:disabled{
    color:#333 !important;
  }
</style>




